
import BScroll from 'better-scroll'
import Badge from 'vux/src/components/badge/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

let count = 1
export default {
name: 'News',
components: {
Badge,
LoadMore
},
data () {
return {
isMoreData: true,
data: [],
pulldownMsg: '下拉刷新',
pullupMsg: '加载更多',
alertHook: 'none',
params: {
merchantId: '',
page: 1,
pageSize: 10
}
}
},
methods: {
getDetails (item) {
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchGet('/merchant/center/update/message/status', {messageId: item.id}).then((res) => {
this.$vux.loading.hide()
if (item.type == 7) {
this.$router.push({
name: 'goodsDetail',
query: {
tabIndex: 0,
id: item.bizId
}
})
} else if (item.type == 8 || item.type == 13) {
this.$router.push({
name: 'hotelManage'
})
} else if (item.type == 10 || item.type == 12) {
this.$router.push({
name: 'myMoney'
})
} else if (item.type == 14) {
this.$router.push({
name: 'goodsDetail',
query: {
tabIndex: 0,
id: item.bizId
}
})
} else if (item.type == 15) {
this.$router.push({
name: 'houseDetail',
query: {
tabIndex: 0,
id: item.bizId
}
})
} else if (item.type == 16) {
this.$router.push({
name: 'houseDetail',
query: {
tabIndex: 0,
id: item.bizId
}
})
} else {
this.$router.push({
name: 'newsDetail',
query: {
title: item.title,
info: item.info,
messageDate: item.messageDate
}
})
}
})
},
getData () {
this.$http.fetchGet('/merchant/center/get/message/list', this.params).then((res) => {
this.data = res.data.data.messageList
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.initScroll()
})
},
initScroll () {
this.$nextTick(() => {
this.scroll = new BScroll(this.$refs.wrapper, { // 初始化better-scroll
probeType: 1, // 1 滚动的时候会派发scroll事件，会截流。2滚动的时候实时派发scroll事件，不会截流。 3除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
click: true 
})

this.scroll.on('scroll', (pos) => {
if (pos.y > 30) {
this.pulldownMsg = '释放立即刷新'
}
})

this.scroll.on('touchEnd', (pos) => { // 上拉刷新
if (pos.y > 30) {
this.refreshData()
} else if (pos.y < (this.scroll.maxScrollY - 30)) { // 下拉加载
this.loadMoreData()
}
})
})
},
refreshData () {
this.$refs.content.style.height = 'auto'
this.pullupMsg = '加载中。。。'
this.params.page = 1
this.$http.fetchGet('/merchant/center/get/message/list', this.params).then((res) => {
this.data = res.data.data.messageList
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}

this.pullupMsg = '加载更多'

this.scroll.refresh()
})
},
loadMoreData () {
if (this.data.length < this.params.pageSize || this.data.length < this.params.pageSize * this.params.page) {
this.isMoreData = false
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.params.page++
this.$http.fetchGet('/merchant/center/get/message/list', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
}
if (res.data.data.messageList.length > 0) {
res.data.data.messageList.map((item) => {
this.data.push(item)
})
} else {
this.isMoreData = false
}
console.log(this.data)

this.pulldownMsg = '下拉刷新'

this.scroll.refresh()
})
}
},
created () {
this.$vux.loading.show({
text: '加载中...'
})
this.params.merchantId = this.$store.state.merchantId
this.getData()
}
}



